import React from 'react';
import { lazy } from "react";
import { HashRouter, Routes, Route } from 'react-router-dom';

// Import Global CSS Files
import "bootstrap/dist/css/bootstrap.min.css";
import 'bootstrap-icons/font/bootstrap-icons.css';
import "./assets/css/style.css";
import "./assets/css/responsive.css";

// Import Layouts
import DefaultLayout from './layouts/DefaultLayout';


// Loader
import Loader from './component/elements/Loader';

// Import Pages
const Home = lazy(() => import('./views/Home'));
const JobListing = lazy(() => import('./views/JobListing'));
const JobDetails = lazy(() => import('./views/JobDetails'));
const Resume = lazy(() => import('./views/Resume'));
const PersonalInformation = lazy(() => import('./views/PersonalInformation'));
const ProfessionalDetails = lazy(() => import('./views/ProfessionalDetails'));
const AddSkills = lazy(() => import('./views/AddSkills'));
const Login = lazy(() => import('./views/Login'));
const Signup = lazy(() => import('./views/Signup'));
const ForgotPassword = lazy(() => import('./views/ForgotPassword'));
const ResetPassword = lazy(() => import('./views/ResetPassword'));
const Otp = lazy(() => import('./views/Otp'));
const SavedJobs = lazy(() => import('./views/SavedJobs'));
const AboutUs = lazy(() => import('./views/AboutUs'));
const TermsConditions = lazy(() => import('./views/TermsConditions'));
const Notifications = lazy(() => import('./views/Notifications'));
const ContactUs = lazy(() => import('./views/ContactUs'));
const SubscriptionPlan = lazy(() => import('./views/SubscriptionPlan'));
const EditProfile = lazy(() => import('./views/EditProfile'));

//routes
// import routes from "./routes";

function App() {
  return (
    <HashRouter>
      <React.Suspense fallback={<Loader/>}>
        <Routes>
          <Route element={<DefaultLayout/>} >
              <Route path="/" element={<Home/>} ></Route>
              <Route path="/login" element={<Login/>} ></Route>
              <Route path="/signup" element={<Signup/>} ></Route>
              <Route path="/forgotPassword" element={<ForgotPassword/>} ></Route>
              <Route path="/resetPassword" element={<ResetPassword/>} ></Route>              
              <Route path="/otp" element={<Otp/>} ></Route>              
              <Route path="/editProfile" element={<EditProfile/>} ></Route>              
              <Route path="/joblist" element={<JobListing/>} ></Route>
              <Route path="/jobDetails" element={<JobDetails/>} ></Route>
              <Route path="/resume" element={<Resume/>} ></Route>
              <Route path="/createResume/PersonalInformation" element={<PersonalInformation/>} ></Route>
              <Route path="/createResume/ProfessionalDetails" element={<ProfessionalDetails/>} ></Route>
              <Route path="/createResume/addSkills" element={<AddSkills />} ></Route>
              <Route path="/savedjobs" element={<SavedJobs/>} ></Route>
              <Route path="/aboutus" element={<AboutUs/>} ></Route>
              <Route path="/termsConditions" element={<TermsConditions/>} ></Route>
              <Route path="/contactus" element={<ContactUs />} ></Route>
              <Route path="/notifications" element={<Notifications />} ></Route>
              <Route path="/subscriptionPlan" element={<SubscriptionPlan />} ></Route>
          </Route>
        </Routes>
      </React.Suspense>
    </HashRouter>
  );
}

export default App;
